@charset "UTF-8";
.alphabet-list .inner .item p { background: url("../../images/space.png") 0 0 repeat; filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000,endColorstr=#99000000); background: rgba(0, 0, 0, 0.6); }
.alphabet-list .inner .item sub { background: transparent; background: url("../../images/space.png") 0 0 repeat; }
.alphabet-list .inner .item sub:hover { background: rgba(0, 0, 0, 0.3); filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#4c000000,endColorstr=#4c000000); }

/*标题，抬头css*/
.webmain .con { margin-bottom: 45px; }

.webmain .con .title { height: 60px; overflow: hidden; background: url(../../images/line_contitle.gif) center center repeat-x; margin-bottom: 15px; position: relative; }

.webmain .con .title a.more { display: block; position: absolute; right: 0; top: 16px; background: url(../../images/more_contitle.gif); width: 130px; height: 30px; text-indent: -99em; overflow: hidden; }

.webmain .con .title h2 { display: block; width: 140px; height: 60px; background-image: url(../../images/p_performing_title.jpg); background-repeat: no-repeat; text-indent: -99em; overflow: hidden; float: left; }

.webmain .con .title ul { float: left; height: 26px; background: #fff; padding: 17px 5px 17px 5px; }

.webmain .con .title ul li { float: left; font-size: 16px; height: 26px; line-height: 26px; overflow: hidden; margin-right: 8px; }

.webmain .con .title ul li a { display: block; width: 100%; height: 26px; line-height: 26px; cursor: pointer; padding: 0 8px; }

.webmain .con .title ul li.c a { background: #000; color: #fff; }

/*导航栏*/
.navTitle { color: #ccc; margin-bottom: 30px; }

.navTitle a { color: inherit; }

.navTitle a:hover { color: red; }

.navTitle .loginfo { float: right; margin: 0 20px 0 15px; }

.navTitle .loginfo .face { display: inline-block; float: left; width: 20px; height: 20px; background: url("../../images/icon-logface.jpg") 50% no-repeat; }

.navTitle .loginfo .name { display: inline-block; float: left; height: 20px; margin-left: 15px; line-height: 20px; cursor: pointer; }

.navTitle .loginfo .name:after { content: ""; display: block; position: absolute; margin-left: 55px; margin-top: -10px; border-top: 6px solid #000; border-right: 6px solid #fff; border-left: 6px solid #fff; }

.allstars .toptool { height: 50px; }
.allstars .toptool .upload { float: right; width: 120px; height: 35px; color: #fff; background-color: #000; }

.allstars-main .sorting { float: left; }
.allstars-main .sorting li { width: 180px; height: 60px; line-height: 60px; text-align: center; background-color: #fff; cursor: pointer; }
.allstars-main .sorting li.active { color: #fff; background-color: #000; }
.allstars-main .sorting li + li { border-top: 1px solid #ccc; }
.allstars-main .list { margin-left: 205px; }
.allstars-main .list .search { height: 30px; margin-top: 20px; }
.allstars-main .list .search ul { float: right; }
.allstars-main .list .search ul li { float: left; width: 35px; height: 20px; color: #ccc; }

.alphabet-list .inner .item, .alphabet-list .inner .item img, .alphabet-list .inner .item sub { width: 180px; height: 260px; }

.alphabet-list .title h3 { position: absolute; width: 50px; height: 60px; text-align: center; line-height: 60px; font-weight: bold; background-color: #fff; }

.alphabet-list .inner .item { float: left; margin-left: 19px; margin-bottom: 15px; }
.alphabet-list .inner .item p { height: 30px; width: 180px; color: #fff; line-height: 30px; text-align: center; margin-top: -30px; }
.alphabet-list .inner .item sub { position: absolute; margin-top: -260px; }

